












import Vue from 'vue';
import Component from 'vue-class-component';
import {FileMlt} from '../hukutemp';
@Component({
	props:{
		list:Array,
		state:null
	},
})
export default class TreeView extends Vue {
	list:Array<any>;
	selection:any;
	title:string;
	state:any;
	constructor(){
		super();
	}
	clickFileName(e:FileMlt){
		this.state.getFileMlt(e);
	}
}




